@font-face {
    font-family: 'aa_bebas_neueregular';
    src: url('../font/aa-bebasneue-webfont.eot');
    src: url('../font/aa-bebasneue-webfont.eot?#iefix') format('embedded-opentype'), url('../font/aa-bebasneue-webfont.woff2') format('woff2'), url('../font/aa-bebasneue-webfont.woff') format('woff'), url('../font/aa-bebasneue-webfont.ttf') format('truetype'), url('../font/aa-bebasneue-webfont.svg#aa_bebas_neueregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'distant_galaxyregular';
    src: url('../font/DISTGRG_-webfont.woff') format('woff'), url('../font/DISTGRG_-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@keyframes star {
    0% {
        opacity: 0;
        transform: perspective(400px) translateZ(450px) translateY(70%);
    }
    7% {
        opacity: 0
    }
    13% {
        opacity: 1;
    }
    100% {
        transform: perspective(400px) translateZ(0) translate(0, 0);
    }
}

.star {
    animation: star 20s linear;
}

.show-element {
    opacity: 1;
    transition: .3s;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.container::after,
.logo::after,
.name::after,
.coins::after,
.register::after {
    content: "";
    display: table;
    clear: both;
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-size: 16px;
}

body {
    background: url("../img/starbg.jpg") repeat scroll center 0;
}

.wrapper {
    position: relative;
    min-height: 100%;
    overflow: hidden;
}

.container {
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 105px;
}

.ttt {
    transform: perspective(400px);
}

.logo,
.name {
    text-align: center;
}

.logo-img {
    width: 100%;
    margin: 50px 0;
}

.name {
    position: relative;
    top: 300px;
    opacity: 0;
}

.name-header {
    margin: 0;
    color: #fff;
    font-family: distant_galaxyregular;
    text-shadow: 0px 0px 60px rgba(255, 232, 31, .7);
    font-size: 4.4375rem;
    font-weight: 100;
    letter-spacing: 1px;
    text-transform: uppercase;
    padding-bottom: 35px;
}

.coins {
    position: relative;
    height: 390px;
    padding-top: 30px;
}

.coins input {
    display: none;
}

.coins label {
    width: 25%;
    position: absolute;
    display: inline-block;
    cursor: pointer;
    padding: 10px;
}

.coins-img {
    position: relative;
    width: 100%;
}

.coins-label-1,
.coins-label-2 {
    background-color: #fff;
    border-radius: 50%;
    margin-left: 20%;
    transform: scale(0)
}

.coins-label-2 {
    background-color: #f00;
    margin-left: 55%;
}

.coins-label-1,
.coins-label-2,
.coins-label-3 {
    -webkit-transition: .3s linear;
    -moz-transition: .3s linear;
    -ms-transition: .3s linear;
    -o-transition: .3s linear;
    transition: .3s linear;
}

.coins-label-1:hover,
.coins-label-2:hover {
    box-shadow: #fff 0 0 45px 3px;
    -webkit-transition: .2s linear;
    -moz-transition: .2s linear;
    -ms-transition: .2s linear;
    -o-transition: .2s linear;
    transition: .2s linear;
}

.coins-label-2:hover {
    box-shadow: #ff0000 0 0 45px 3px;
}

input[type=radio]:checked+label.coins-label-1 {
    box-shadow: #fff 0 0 45px 3px;
}

input[type=radio]:checked+label.coins-label-2 {
    box-shadow: #ff0000 0 0 45px 3px;
}

.register {
    position: relative;
    z-index: -1;
    opacity: 0;
    color: #ffffff;
    margin: 25px -10px;
    padding-left: 20px;
    text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.8);
}

.register-button,
.register-mail,
.register-social {
    float: left;
}

.mail-info,
.social-info {
    font-family: distant_galaxyregular;
    font-size: 12px;
    margin: 0 0 12px;
    text-shadow: 0px 0px 30px rgba(255, 232, 31, .7);
}

.socials-list {
    padding: 0;
    margin: 0;
    position: relative;
}

.socials-i {
    display: inline-block;
    margin: 0 5px;
}

.social-img {
    cursor: pointer;
    -webkit-filter: drop-shadow(4px 0px 9px rgba(0, 0, 0, 0.54));
    filter: drop-shadow(4px 0px 9px rgba(0, 0, 0, 0.54));
    display: block;
    border: 0;
    outline: 0;
}

.fb {
    background: transparent url("../img/Face.svg") no-repeat;
    width: 20px;
    height: 43px;
}

.vk {
    background: transparent url("../img/VK-01.svg") no-repeat;
    width: 54px;
    height: 31px;
}

.ok {
    background: transparent url("../img/ok.svg") no-repeat;
    width: 25px;
    height: 44px;
}

.mr {
    background: transparent url("../img/@-01.svg") no-repeat;
    width: 35px;
    height: 35px;
}

.register-mail {
    padding-left: 48px;
}

.mail-input {
    outline: none;
    border: 3px solid rgba(255, 232, 31, 1);
    border-radius: 10px;
    width: 357px;
    height: 53px;
    font-size: 1.8em;
    padding: 0 .5em;
    background: rgb(0, 0, 0);
    color: #fff;
    transition: .6s;
}

.mail-input-error {
    border: 3px solid #ff0000;
    transition: .6s;
}

.mail-input-error-text,
.mail-input-error-text2 {
    position: absolute;
    font-size: 12px;
    font-family: Arial, sans-serif;
    color: #ff0000;
    text-shadow: none;
    font-family: distant_galaxyregular;
}

.error {
    visibility: visible;
}

.input-error-text {
    margin: 5px 0 0 0;
}

.register-button {
    padding: 24px 0 0 43px;
}

.button-input {
    cursor: pointer;
    outline: none;
    height: 53px;
    line-height: 51px;
    width: 357px;
    background: rgba(0, 0, 0, 0.699);
    font-family: distant_galaxyregular;
    font-size: 39px;
    letter-spacing: 1px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    border: 3px solid rgba(255, 232, 31, 1);
    border-radius: 10px;
    transition: all .2s;
    text-align: center;
}

.button-input:hover {
    box-shadow: 0 0 13px 2px rgba(255, 232, 31, 1);
    background: rgba(255, 232, 31, 1);
    color: #000;
    transition: all .2s;
}


/* Footer */

#footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    height: 105px;
    background: #241f26;
    display: flex;
    box-orient: horizontal;
    flex-direction: row;
    box-pack: center;
    justify-content: center;
    box-align: center;
    align-items: center;
    opacity: 0.9;
}

#footer img {
    max-width: 100%;
    margin: 0 1em;
}

.img1 {
    width: 6%;
    opacity: .7;
}

.opacityImg {
    opacity: .5;
}

#footer .support-img {
    margin-top: -1em;
    opacity: 1;
}

#footer>span {
    color: #504e51;
    text-transform: uppercase;
    text-decoration: none;
    font-family: "Verdana", Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 1.35px;
    display: inline-block;
    margin-top: .3em;
}

.show-element {
    opacity: 1;
    transition: .7s;
}